import React, { Component } from 'react';
import Hero from "../assets/images/webdevelopment.png";

class Home extends Component {
    render() {
        return (
            <div>
         <img style={{maxWidth:"100%",}} src={Hero} className="hero-home" alt="pencil pens webdevelopment portfolio"/>
         
            </div>
        );
        
    }
}

export default Home;